let docGroups = document.querySelectorAll(".doc-group")
docGroups.forEach(group => {
    let openBtn = group.querySelector(".doc-card__btn")
    let closeBtn = group.querySelector(".doc-modal__close")

    openBtn.onclick = function () {
        group.classList.add("_open-modal")
        body.classList.add("fixed-body")
    }
    closeBtn.onclick = function () {
        group.classList.remove("_open-modal")
        body.classList.remove("fixed-body")
    }
})
