let header = document.querySelector(".header")
let toggleMenuButtons = document.querySelectorAll(".burger")

toggleMenuButtons.forEach(btn => {
    btn.onclick = function () {
        header.classList.toggle("_open")
        body.classList.toggle("fixed-body")
    }
})

let headerUser = document.querySelector(".header-user")
if(headerUser){
    let openBtn = headerUser.querySelector(".header-user__btn")
    openBtn.onclick = function () {
        headerUser.classList.toggle("_open")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(headerUser);

        if ( ! withinBoundaries ) {
            if(headerUser.classList.contains("_open")){
                headerUser.classList.remove("_open")
            }
        }
    })
}
