let noteCards = document.querySelectorAll(".note-card")
noteCards.forEach(card => {
    let checkbox = card.querySelector(".m-switch input")
    if(!checkbox){
        return
    }
    setCardState()
    checkbox.onchange = function () {
        setCardState()
    }

    function setCardState() {
        if(checkbox.checked){
            card.classList.add("_active")
        }else{
            card.classList.remove("_active")
        }
    }
})

let medalModalImg = document.querySelector(".medal-modal__img")
let medalModalItems = document.querySelectorAll(".medal-modal__item")
if(medalModalImg){
    let medalImg = medalModalImg.querySelector("img")
    medalModalItems.forEach(item => {
        item.onmouseover = function () {
            let imgSrc = item.getAttribute("data-img-src")
            medalImg.setAttribute("src",imgSrc)
            medalModalImg.classList.add("_show")
        }
    })
}

